@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMonoRegular.woff2") format("woff2"), url("../fonts/PTMonoRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensedRegular.woff2") format("woff2"), url("../fonts/RobotoCondensedRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensedBold.woff2") format("woff2"), url("../fonts/RobotoCondensedBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page__body-wrapper {
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 320px; }

.page-main {
  flex: 1 1 auto; }

.page-header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  position: fixed;
  width: 100%; }
  @media (max-width: 1023px) {
    .page-header {
      position: absolute; } }

.page-header__container {
  max-width: 1222px;
  margin: 0 auto;
  display: flex;
  min-height: 109px;
  align-items: center;
  padding: 0 15px; }
  @media (max-width: 1023px) {
    .page-header__container {
      min-height: 95px;
      padding: 0 50px; } }
  @media (max-width: 767px) {
    .page-header__container {
      min-height: 65px;
      padding: 0 26px; } }

.page-header__contacts {
  flex: 1 1 auto; }

.page-header__button {
  flex: 0 0 222px; }
  @media (max-width: 1023px) {
    .page-header__button {
      flex: 0; } }

.logo {
  font-size: 0; }

.logo__wrapper {
  padding-top: 4px; }
  @media (max-width: 1023px) {
    .logo__wrapper {
      padding-top: 0; } }

.logo__main {
  display: none; }

.logo__text {
  fill: #00b2ff; }
  @media (max-width: 767px) {
    .logo__text {
      display: none; } }

.logo__brain {
  display: none;
  fill: #00b2ff; }
  @media (max-width: 767px) {
    .logo__brain {
      fill: #00b2ff;
      display: block; } }

.logo__text:hover {
  fill: #c7c9ca; }

.logo__text:active {
  fill: #f2f5f6; }

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 460px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .contacts__wrapper {
      max-width: 100px;
      padding-bottom: 20px;
      margin: 0 0 0 74%; } }
  @media (max-width: 767px) {
    .contacts__wrapper {
      max-width: 103px;
      padding-bottom: 10px;
      margin: 0 4px 0 auto; } }

.contacts__link {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: #f2f5f6;
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px; }
  @media (max-width: 1023px) {
    .contacts__link {
      font-size: 0; } }

.contacts__link::before {
  content: "";
  position: absolute;
  top: -9px;
  left: -42px;
  width: 35px;
  height: 35px; }

.contacts__link--mail::before {
  background: url("../img/svg/email.svg") no-repeat center; }

.contacts__link--phone::before {
  background: url("../img/svg/phone-call.svg") no-repeat center; }

.contacts__mail {
  padding-left: 32px; }
  @media (max-width: 1023px) {
    .contacts__mail {
      padding-left: 43px; } }

.button__call {
  display: inline-block;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  text-decoration: none;
  color: #f2f5f6;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  padding: 11px 18px; }
  @media (max-width: 1023px) {
    .button__call {
      display: none; } }

.button__call:hover {
  background-color: #f2f5f6;
  color: #041827; }

.button__call:focus {
  background-color: #f2f5f6;
  color: #041827; }

.button__call:active {
  background-color: #c7c9ca;
  color: #041827; }

.button__advise span {
  display: inline-block;
  background-color: #00b2ff;
  border-radius: 3px;
  text-decoration: none;
  color: #f2f5f7;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 18px 27px; }
  @media (max-width: 767px) {
    .button__advise span {
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 5%;
      padding: 12px 37px; } }

.button__advise span:hover {
  background-color: #041827;
  color: #c7c9ca;
  border-radius: 3px; }

@media (max-width: 767px) {
  .button__advise span:nth-child(2n+1) {
    display: none; } }

@media (min-width: 768px) {
  .button__advise span:nth-child(3n+2) {
    display: none; } }

.promo {
  background-color: #041827; }

.promo__title-container {
  width: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(to bottom, rgba(25, 32, 36, 0.7)), url("../../img/back-desktop.png") no-repeat center;
  background-size: cover; }
  @media (max-width: 1023px) {
    .promo__title-container {
      background: linear-gradient(to bottom, rgba(25, 32, 36, 0.7)), url("../../img/back-tablet.png") no-repeat center;
      background-size: cover; } }
  @media (max-width: 767px) {
    .promo__title-container {
      background: linear-gradient(to bottom, rgba(25, 32, 36, 0.7)), url("../../img/back-mobile.png") no-repeat center;
      background-size: cover; } }

.promo__title-wrapper {
